/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
// import { useSelect } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { registerBlockType } from '@wordpress/blocks';
import {
	BlockControls,
	InspectorControls,
	InnerBlocks,
	BlockVerticalAlignmentToolbar,
	useBlockProps,
} from '@wordpress/block-editor';
import {
	PanelBody,
	ToggleControl,
	Tooltip,
	Button,
	BaseControl,
	Flex,
	FlexItem,
} from '@wordpress/components';
import { link, linkOff } from '@wordpress/icons';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import deprecated from './deprecated';
import blockIcon from './icon';
import {
	ArkbProOnly,
	ArkbDeviceTab,
	LsSpaceControl,
	ArkbColumnWidthControl,
	DynamicStyles,
	ArkbLinkControl,
} from '@components';
import { getDynamicStyles } from '../columns/_helper';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * blockName
 */
const blockName = 'ark-block-column';

/**
 * see: https://github.com/WordPress/gutenberg/blob/899286307b/packages/components/src/box-control/linked-button.js
 */
const LinkedButton = ({ isLinked, ...props }) => {
	const label = isLinked ? __('Unlink Sides') : __('Link Sides');

	return (
		<Tooltip text={label}>
			<span className='__link'>
				<Button
					{...props}
					className='component-box-control__linked-button'
					isPrimary={isLinked}
					isSecondary={!isLinked}
					isSmall
					icon={isLinked ? link : linkOff}
					iconSize={16}
					aria-label={label}
				/>
			</span>
		</Tooltip>
	);
};

/**
 * リッチカラム-項目
 */
registerBlockType(metadata.name, {
	title: __('Column item', 'arkhe-blocks'), //'自由項目',
	icon: blockIcon,
	edit: ({ attributes, setAttributes, clientId, context }) => {
		const {
			vAlign,
			columnWidth,
			isBreakAll,
			padding,
			paddingTab,
			paddingMB,
			href,
			rel,
			isNewTab,
			ariaLabel,
			useAtag,
		} = attributes;
		const parentIsScrollable = context['arkb/columns/isScrollable'];

		const [isWidthLinked, setIsWidthLinked] = useState(false);

		// 子ブロックの設定
		const blockClass = classnames(blockName, 'ark-keep-mt--s', 'ark-has-guide', {
			'is-break-all': isBreakAll,
		});

		const columnStyle = {
			'--arkb-clmn-w--pc': columnWidth?.pc || null,
			'--arkb-clmn-w--tab': columnWidth?.tab || null,
			'--arkb-clmn-w--mb': columnWidth?.mobile || null,
		};

		// const hasChildBlocks = useSelect(
		// 	(select) => {
		// 		const { getBlockOrder } = select('core/block-editor');
		// 		return getBlockOrder(clientId).length > 0;
		// 	},
		// 	[clientId]
		// );

		const setLink = ({ url: newUrl, rel: newRel, opensInNewTab }) => {
			if (!IS_PRO) return;

			if (newUrl) {
				setAttributes({ href: newUrl || undefined });
			}

			// undefinedチェックはコアのバグに対応するため
			if (typeof opensInNewTab !== 'undefined') {
				setAttributes({
					isNewTab: opensInNewTab,
					rel: newRel,
				});
			}
		};

		const blockProps = useBlockProps({
			className: blockClass,
			'data-valign': vAlign || null,
			style: columnStyle || null,
		});

		const innerBlocksOptions = {
			template: [['core/paragraph']],
			templateLock: false,
			// renderAppender: hasChildBlocks ? undefined : InnerBlocks.ButtonBlockAppender,
		};
		const innerBlocksProps = useInnerBlocksProps(blockProps, innerBlocksOptions);

		// const hasLink = !!href;

		// const innerBlocksProps = hasLink
		// 	? useInnerBlocksProps({}, innerBlocksOptions)
		// 	: useInnerBlocksProps(blockProps, innerBlocksOptions);

		const dynamicStyles = getDynamicStyles({ padding, paddingTab, paddingMB, clientId });

		return (
			<>
				{dynamicStyles && <DynamicStyles style={dynamicStyles} />}
				<BlockControls>
					<BlockVerticalAlignmentToolbar
						onChange={(value) => {
							setAttributes({ vAlign: value });
						}}
						value={vAlign}
					/>
					<ArkbLinkControl
						isPro={true}
						attrs={{ url: href, rel, isNewTab, ariaLabel, useAtag }}
						setLink={setLink}
						removeLink={() => {
							setAttributes({
								href: undefined,
								rel: undefined,
								isNewTab: false,
								ariaLabel: undefined,
								useAtag: false,
							});
						}}
						setRel={(val) => {
							if (!IS_PRO) return;
							setAttributes({ rel: val || undefined });
						}}
						setAriaLabel={(val) => {
							if (!IS_PRO) return;
							setAttributes({ ariaLabel: val || undefined });
						}}
						setUseAtag={(val) => {
							if (!IS_PRO) return;
							setAttributes({ useAtag: val || false });
						}}
					/>
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')}>
						<ToggleControl
							label={__(
								'Forcibly breaks the character string according to the display range',
								'arkhe-blocks'
							)}
							help={__('"word-break: break-all" is applied.', 'arkhe-blocks')}
							checked={isBreakAll}
							onChange={(val) => {
								setAttributes({ isBreakAll: val });
							}}
						/>
						<ArkbProOnly>
							<BaseControl className='u-mb-20'>
								<BaseControl.VisualLabel>
									<Flex style={{ paddingBottom: '4px' }}>
										<FlexItem style={{ marginRight: 'auto' }}>
											{__('Column width', 'arkhe-blocks')}
										</FlexItem>
										<FlexItem>
											<LinkedButton
												onClick={() => {
													setIsWidthLinked(!isWidthLinked);
												}}
												isLinked={isWidthLinked}
											/>
										</FlexItem>
									</Flex>
								</BaseControl.VisualLabel>
								<ArkbColumnWidthControl
									{...{
										columnWidth,
										setAttributes: IS_PRO ? setAttributes : null,
										defaultUnit: parentIsScrollable ? 'px' : '%',
										isLinked: isWidthLinked,
									}}
								/>
							</BaseControl>
							<BaseControl>
								<BaseControl.VisualLabel>
									{__('Padding in cloumn', 'arkhe-blocks')}
								</BaseControl.VisualLabel>
								<ArkbDeviceTab
									className='-padding -triple'
									controlPC={
										<LsSpaceControl
											name='padding'
											value={padding}
											setAttributes={IS_PRO ? setAttributes : null}
											clearable={true}
										/>
									}
									controlTab={
										<LsSpaceControl
											name='paddingTab'
											value={paddingTab}
											setAttributes={IS_PRO ? setAttributes : null}
											clearable={true}
											successionHelp={__('PC setting', 'arkhe-blocks')}
										/>
									}
									controlMB={
										<LsSpaceControl
											name='paddingMB'
											value={paddingMB}
											setAttributes={IS_PRO ? setAttributes : null}
											clearable={true}
											successionHelp={__('Tablet setting', 'arkhe-blocks')}
										/>
									}
								/>
							</BaseControl>
						</ArkbProOnly>
					</PanelBody>
				</InspectorControls>
				<div {...innerBlocksProps} />
			</>
		);
	},
	save: ({ attributes }) => {
		const { vAlign, isBreakAll, href, rel, isNewTab, ariaLabel, useAtag } = attributes;

		const blockPropsData = {
			className: classnames(`${blockName} ark-keep-mt--s`, {
				'is-break-all': isBreakAll,
			}),
			'data-valign': vAlign || null,
		};

		const hasLink = !!href;

		// linkboxで出力
		if (hasLink && !useAtag) {
			const blockProps = useBlockProps.save({
				...blockPropsData,
				tabIndex: '0',
				role: 'link',
				'aria-label': ariaLabel || null,
				'data-arkb-linkbox': '1',
			});

			return (
				<div {...blockProps}>
					{/* linkbox入れ子になっても大丈夫なように先頭に a を置いておく */}
					<a
						data-arkb-link='1'
						aria-hidden='true'
						{...{
							href,
							rel: rel || null,
							target: isNewTab ? '_blank' : null,
						}}
					>
						{ariaLabel || ''}
					</a>
					<InnerBlocks.Content />
				</div>
			);
		}

		// aタグで出力
		if (hasLink && useAtag) {
			const blockProps = useBlockProps.save({
				...blockPropsData,
				href,
				rel: rel || null,
				target: isNewTab ? '_blank' : null,
				'aria-label': ariaLabel || null,
				'data-arkb-link': '1', // attributes取得用のセレクタ
			});
			return (
				<a {...blockProps}>
					<InnerBlocks.Content />
				</a>
			);
		}

		// 通常
		const blockProps = useBlockProps.save(blockPropsData);
		return (
			<div {...blockProps}>
				<InnerBlocks.Content />
			</div>
		);
	},
	deprecated,
});
