import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import classnames from 'classnames';

const blockName = 'ark-block-column';

export default [
	{
		supports: {
			anchor: true,
			className: false,
			reusable: false,
		},
		attributes: {
			vAlign: {
				type: 'string',
				default: '',
			},
			columnWidth: {
				type: 'object',
			},
			isBreakAll: {
				type: 'boolean',
				default: false,
			},
			useCustomPadding: {
				type: 'boolean',
				default: false,
			},
			padding: {
				type: 'object',
				default: {
					top: '1rem',
					left: '1rem',
					right: '1rem',
					bottom: '1rem',
				},
			},
		},
		save: ({ attributes }) => {
			const { vAlign, columnWidth, isBreakAll, useCustomPadding, padding } = attributes;

			const columnStyle = {
				'--arkb-clmn-w--pc': columnWidth?.pc || null,
				'--arkb-clmn-w--tab': columnWidth?.tab || null,
				'--arkb-clmn-w--mb': columnWidth?.mobile || null,
			};

			// 内部padding
			if (useCustomPadding) {
				const pad = `${padding.top} ${padding.right} ${padding.bottom} ${padding.left}`;
				columnStyle['--arkb-clmn-pddng'] = pad;
			}

			const blockProps = useBlockProps.save({
				className: classnames(`${blockName} ark-keep-mt--s`, {
					'is-break-all': isBreakAll,
				}),
				'data-valign': vAlign || null,
				style: columnStyle || null,
			});

			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
	{
		supports: {
			anchor: true,
			className: false,
			reusable: false,
		},
		attributes: {
			vAlign: {
				type: 'string',
				default: '',
			},
			widthPC: {
				type: 'number',
			},
			widthTab: {
				type: 'number',
			},
			widthMobile: {
				type: 'number',
			},
			isBreakAll: {
				type: 'boolean',
				default: false,
			},
			useCustomPadding: {
				type: 'boolean',
				default: false,
			},
			padding: {
				type: 'object',
				default: {
					top: '1rem',
					left: '1rem',
					right: '1rem',
					bottom: '1rem',
				},
			},
		},
		migrate: (attributes) => {
			const newAttrs = { ...attributes };
			const newWidthAttr = {};

			if (attributes.widthMobile) {
				newAttrs.widthMobile = undefined;
				newWidthAttr.mobile = attributes.widthMobile + '%';
			}
			if (attributes.widthTab) {
				newAttrs.widthTab = undefined;
				newWidthAttr.tab = attributes.widthTab + '%';
			}
			if (attributes.widthPC) {
				newAttrs.widthPC = undefined;
				newWidthAttr.pc = attributes.widthPC + '%';
			}

			if (Object.keys(newWidthAttr).length > 0) {
				newAttrs.columnWidth = newWidthAttr;
			}

			return newAttrs;
		},
		save: ({ attributes }) => {
			const {
				vAlign,
				widthPC,
				widthTab,
				widthMobile,
				isBreakAll,
				useCustomPadding,
				padding,
			} = attributes;

			const columnStyle = {};
			if (widthMobile) {
				columnStyle['--arkb-fb'] = widthMobile + '%';
			}
			if (widthTab) {
				columnStyle['--arkb-fb_tab'] = widthTab + '%';
			}
			if (widthPC) {
				columnStyle['--arkb-fb_pc'] = widthPC + '%';
			}

			// 内部padding
			if (useCustomPadding) {
				const pad = `${padding.top} ${padding.right} ${padding.bottom} ${padding.left}`;
				// const padSP = `${paddingSP.top} ${paddingSP.right} ${paddingSP.bottom} ${paddingSP.left}`;
				columnStyle['--arkb-clmn-pddng'] = pad;
			}

			const blockProps = useBlockProps.save({
				className: classnames(`${blockName} ark-keep-mt--s`, {
					'is-breadk-all': isBreakAll,
				}),
				'data-valign': vAlign || null,
				style: columnStyle || null,
			});

			return (
				<div {...blockProps}>
					<InnerBlocks.Content />
				</div>
			);
		},
	},
];
