/**
 * @Internal dependencies
 */
import { getCusomPadding, convertStylesToCssCode } from '@helper';

export function getDynamicStyles({ gap, gapTab, gapMB, padding, paddingTab, paddingMB, clientId }) {
	const styles = {
		all: {
			'--arkb-gap--x': gap?.x || '',
			'--arkb-gap--y': gap?.y || '',
			'--arkb-padding': padding
				? getCusomPadding(padding, 'var(--arkb-padding--default)')
				: '',
		},
		sp: {
			'--arkb-gap--x': gapTab?.x || '',
			'--arkb-gap--y': gapTab?.y || '',
			'--arkb-padding': paddingTab
				? getCusomPadding(paddingTab, 'var(--arkb-padding--default)')
				: '',
		},
		mb: {
			'--arkb-gap--x': gapMB?.x || '',
			'--arkb-gap--y': gapMB?.y || '',
			'--arkb-padding': paddingMB
				? getCusomPadding(paddingMB, 'var(--arkb-padding--default)')
				: '',
		},
	};

	return convertStylesToCssCode(`#block-${clientId}`, styles);
}
